.section_topstory_folder {
    padding-top: 0;
}

.wrapper-topstory-folder.wrapper-topstory-folder-v2 {
    min-height: 300px;

    .article-topstory {
        background: #fff;
        padding-right: 0;
    }

    .list-sub-feature {
        li {
            .thumb-art {
                display: none
            }

            .description {
                color: #5F5F5F;
            }
        }
    }
}

.article-topstory {
    .title-news {
        font-weight: bold;
        padding-top: 0 !important;
    }

    .description {
        font-size: 15px !important;
        color: #5F5F5F;
    }

    &.full-thumb {
        .thumb-art {
            width: 100% !important;
            margin: 0 !important;
            float: none;
        }

        .content {
            background: #F7F7F7;
            padding: 12px 17px;
        }
    }
}

.wrapper-topstory-folder {
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 32px;
    padding-bottom: 20px;
}

.list-news-subfolder {
    .item-news-common {
        .thumb-art {
            float: left;
            width: 241px;
            margin: 0 15px 0 0;
        }

        .description {
            font-size: 16px;
            color: #5F5F5F;
        }
    }
}

.col-left-top {
    .pagination {
        border-top: 0;
        padding-top: 0;
        margin-bottom: 30px;

        .paging {
            justify-content: flex-end;
        }
    }
}

.box_thacmac {
    background: #222222 url(images/graphics/bg_thacmac.png);
    height: 300px;
    position: relative;

    .title {
        color: rgba(255, 255, 255, 0.56);
        font-size: 16px;
        position: absolute;
        top: 50%;
        transform: translate(0px, -50%);
        padding: 0 36px 0 46px;
        line-height: 1.5;

        &::before {
            content: "";
            position: absolute;
            width: 3px;
            height: 50px;
            background: #F9F8F7;
            left: 32px;
            top: -2px;
        }

        strong {
            color: #fff;
            display: block;
        }
    }
}

.box-taitro {
    text-align: center;

    .title_line_top {
        background: #050507;
        font-size: 18px;
        line-height: 42px;
        color: #fff;
    }

    .wrap {
        padding: 24px 10px;
        border: 1px solid #050507
    }
}

.section_video {
    background: #050507;
    padding: 15px 0;

    .title-box-category {
        font-size: 20px;

        .inner-title {
            color: #fff;

            &::before {
                display: none
            }
        }
    }

    .inner-section-video {
        margin: 0;
        background: none;

        &::before {
            display: none;
        }
    }

    .wrapper-box-video {
        display: flex;

        .box-player-video {
            width: 470px;
            width: calc(100% - 250px);

            .title-news {
                font-size: 17px;

                a {
                    color: #FFFFFF;
                }
            }
        }

        .box-scroll-video {
            width: 250px;
            padding: 0 0 0 30px !important;
            position: static;

            .item-news-common {
                border-top: 0;

                .thumb-art {
                    width: 100%;
                    float: none;
                    margin: 0 0 6px 0
                }

                &:last-of-type {
                    margin-top: 0;
                }
            }

            .title-news {
                font-size: 14px;

                a {
                    color: #F4F4F4;
                }
            }
        }
    }
}

.head-contact {
    margin-bottom: 22px;

    .nav-folder {
        margin-bottom: 10px;

        a {
            font-size: 18px;
            color: #4D4D4D;
            font-weight: bold;
            margin-right: 30px;

            &.active {
                font-weight: 400;
                margin-right: 0;
            }
        }
    }

    .date {
        color: #8D8D8D;
        font-size: 14px;
    }
}

.form-contact {
    max-width: 650px;
    margin: 0 auto;

    .sub-title-page {
        font-size: 28px;
        font-family: Arial;
        font-weight: bold;
    }
}

// CusTom Detail
.box_comment_vne {
    margin-bottom: 20px;
}

.page-detail {
    &.top-detail {
        background: none;
        border: none;

        .container {
            background: #fff;
            flex-wrap: wrap;
        }
    }

    .container {
        background: #fff;
        flex-wrap: wrap;

    }

    .title-box-category {
        font-weight: 400;

        &::before {
            content: '';
            width: 100%;
            height: 1px;
            background: #E5E5E5;
            position: absolute;
            left: 0;
            bottom: 9px
        }

        .inner-title {
            background: #fff;
            padding-right: 10px;

            &:before {
                display: none
            }
        }
    }

    .list-topic {
        flex-wrap: wrap;
        margin: 0 -10px;
        padding-bottom: 0;

        &:before {
            display: none
        }

        .item-news {
            width: 25%;
            border-right: 0;
            padding: 0 10px 20px 10px;
            margin-bottom: 28px;

            .title-news {
                font-size: 16px;
            }
        }
    }
}

.photo-dark {
    .main_menu_seagame {
        margin-bottom: 0;
    }

    .page-detail {
        .container {
            background: none;
        }

        &.top-detail {
            .container {
                background: none;
            }
        }

        &.middle-detail,
        &.bottom-detail {
            .container {
                background: #fff !important;
            }
        }
    }
}

.btn_control_menu {
    display: none
}

.title-box-category .view-more {
    display: none
}

@media screen and (max-width: 1129px) {
    .page-detail.top-detail {
        .sidebar-1 {
            max-width: calc(100% - 360px);
        }
    }

    .social_pin {
        width: 40px;

        .social_left {
            li {
                margin-right: 0;
            }
        }
    }
}

@media screen and (max-width: 979px) {
    .page-detail.top-detail {
        .sidebar-1 {
            max-width: calc(100% - 60px);
        }

        .sidebar-2 {
            display: none
        }
    }

    .social_pin {
        width: 60px;
    }
}

@media screen and (max-width: 600px) {
    .btn_control_menu {
        display: block
    }
}